初始文件模板

<template>
    <transition name="slide">
        <div>
            <div class="head">
                <img src="static/doctor.jpg" class="doctorImg">
                <div class="doctorInfo">
                    <div>
                        <span class="doctorHonour">预约服务: 朱氏头皮针</span>
                    </div> 
                    <div>
                        <span class="verbCost">朱明清</span>
                        <span class="verbCost">教授</span>
                    </div>
                    <div>
                        <span class="verbCost">地址 详细地址</span>
                    </div>
                </div>
            </div>
            <div class="block">
                <block-head :title="'预约时间'"></block-head>
                <p>2017/12/23 星期六 14:20</p>
            </div>
            <div class="block">
                <block-head :title="'会员'"></block-head>
                <div class="vipItem">
                    <p class="itemLeft">姓名</p>
                    <p class="itemRight">孙晓亮</p>
                </div>
                <div class="vipItem">
                    <p class="itemLeft">性别</p>
                    <p class="itemRight">男</p>
                </div>
                <div class="vipItem">
                    <p class="itemLeft">年龄</p>
                    <p class="itemRight">25</p>
                </div>
                <div class="vipItem">
                    <p class="itemLeft">手机号码</p>
                    <p class="itemRight">13888888888</p>
                </div>
            </div>
            <div class="verbBox">
                <div class="verbBtn" @click="goVerbSuccess">
                    <span>确认预约</span>
                </div>
            </div>
        </div>
    </transition>
</template>

<script>

import blockHead from 'components/blockHead/blockHead'

export default {
  data () {
    return {
    }
  },
  methods: {
    goVerbSuccess: function () {
      this.$router.push({
        path: `/verbSuccess`
      })
    }
  },
  components: {
    blockHead
  }
}
</script>

<style scoped lang="stylus" rel="stylesheet/stylus" scoped>
@import "~common/stylus/variable"
@import "~common/stylus/base"

$img_height_doctor = 80px
.doctorImg
    width $img_height_doctor
    height $img_height_doctor
    border-radius $img_height_doctor/2
    border 2px solid $color-theme-blue
    margin-left 20px

.head
    @extend .flex
    @extend .flex-row
    @extend .flex-start
    height $img_height_doctor + 40
    background-color white

.doctorInfo
    @extend .flex
    @extend .flex-column
    justify-content space-around
    text-align left
    margin-left 20px
    height $img_height_doctor
    span
        font-weight $font-weight-level_6

.doctorPosition
    margin-left 10px

.doctorHonour
    font-size $font-size-level_4
    color black

.verbCost
    color $color-theme-gray_1
    font-size $font-size-level_2

.block
    background-color white
    margin-top 5px
    padding 10px
    p
        text-align left
        color $color-theme-gray_2
        font-size $font-size-level_2
        padding-left 16px
        margin-top 10px

.vipItem
    @extend .flex
    @extend .flex-row
    @extend .flex-start
    height $height_level_form
    .itemLeft
        width 100px
        color $color-theme-gray_1
        font-size $font-size-level_2
    .itemRight
        flex-grow 2
        color $color-theme-gray_2
        font-size $font-size-level_2

.verbBox
    @extend .flex
    @extend .flex-row
    @extend .flex-around
    margin-top 20px

.verbBtn
    background-color $color-theme-blue
    width 70%
    height 40px
    border-radius 3px
    span
        line-height 40px
        color white
        font-weight $font-weight-level_7
</style>


